import React, { useState, useEffect } from "react"
import { Container, Row, Card, CardBody, CardHeader, CardTitle, CardImg, Breadcrumb, BreadcrumbItem, Button } from "shards-react"
import axios from "axios"

//Image imports
import shoulders from "../images/shoulders.png"
import bald from "../images/bald.png"
import below from "../images/hairstyle3.png"
import longtop from "../images/hairstylew.png"


export default function Booking() {
  const [section, setSection] = useState("haircut")

  const [haircut, setHaircut] = useState("")
  const [hairstyle, setHairstyle] = useState("")
  const [stylist, setStylist] = useState("")
  const [time, setTime] = useState("")

  useEffect(() => {
    async function start() {
      const res = await axios({
        method: "get",
        url: "http://localhost:8000/api/user"
      })
      console.log(res)
    }
    start()
  }, [section])

  function hairStyle(type) {
    console.log("DONE")
    setSection("Hair Style")
    setHaircut(type)
  }

  function stylistType(type) {
    setSection("Stylist")
    setHairstyle(type)
  }

  function timeStylist(type) {
    setSection("Time")
    setStylist(type)
  }

  function confirm(time) {
    setTime(time)
    setSection("Confirm")
  }

  function showCards() {
    switch (section) {
      case "haircut":
        return (
          <div className="text-area">
            <Breadcrumb>
              <BreadcrumbItem>
                <a href="/">Home</a>
              </BreadcrumbItem>
              <BreadcrumbItem active>Haircut</BreadcrumbItem>
            </Breadcrumb>
            <Card>
              <CardHeader>Book Appointment</CardHeader>
              <CardBody>
                <CardTitle>Select Hair Cut</CardTitle>

                <Card className="cardSize">
                  <CardImg className="cardImage" src={shoulders} />
                  <CardBody>
                    <CardTitle>Shoulder Length</CardTitle>
                    <Button onClick={() => hairStyle("shoulder length")}>Select</Button>
                  </CardBody>
                </Card>

                <Card className="cardSize">
                  <CardImg className="cardImage" src={longtop} />
                  <CardBody>
                    <CardTitle>Long Top</CardTitle>
                    <Button onClick={() => hairStyle("long top")}>Select</Button>
                  </CardBody>
                </Card>

                <Card className="cardSize">
                  <CardImg className="cardImage" src={below} />
                  <CardBody>
                    <CardTitle>Below Shoulders</CardTitle>
                    <Button onClick={() => hairStyle("below shoulders")}>Select</Button>
                  </CardBody>
                </Card>

                <Card className="cardSize">
                  <CardImg className="cardImage" src={bald} />
                  <CardBody>
                    <CardTitle>Bald</CardTitle>
                    <Button onClick={() => hairStyle("bald")} >Select</Button>
                  </CardBody>
                </Card>

              </CardBody>
            </Card>
          </div>

        )

      case "Hair Style":
        return (
          <div className="text-area">
            <Breadcrumb>
              <BreadcrumbItem>
                <a href="/">Home</a>
              </BreadcrumbItem>
              <BreadcrumbItem>Haircut</BreadcrumbItem>
              <BreadcrumbItem active>Hair Style</BreadcrumbItem>
            </Breadcrumb>
            <Card>
              <CardHeader>Book Appointment</CardHeader>
              <CardBody>
                <CardTitle>Select Hair Style</CardTitle>

                <Card className="cardSize">
                  <CardImg className="cardImage" src={shoulders} />
                  <CardBody>
                    <CardTitle>California Waves</CardTitle>
                    <Button onClick={() => stylistType("California Waves")}>Select</Button>
                  </CardBody>
                </Card>

                <Card className="cardSize">
                  <CardImg className="cardImage" src={longtop} />
                  <CardBody>
                    <CardTitle>Wavvy Top</CardTitle>
                    <Button onClick={() => stylistType("California Waves")}>Select</Button>
                  </CardBody>
                </Card>

                <Card className="cardSize">
                  <CardImg className="cardImage" src={below} />
                  <CardBody>
                    <CardTitle>French Bob</CardTitle>
                    <Button onClick={() => stylistType("California Waves")}>Select</Button>
                  </CardBody>
                </Card>

                <Card className="cardSize">
                  <CardImg className="cardImage" src={below} />
                  <CardBody>
                    <CardTitle>Two Tone Messy Bob</CardTitle>
                    <Button onClick={() => stylistType("California Waves")}>Select</Button>
                  </CardBody>
                </Card>

                <Card className="cardSize">
                  <CardImg className="cardImage" src={below} />
                  <CardBody>
                    <CardTitle>Medium Layered</CardTitle>
                    <Button onClick={() => stylistType("California Waves")}>Select</Button>
                  </CardBody>
                </Card>

                <Card className="cardSize">
                  <CardImg className="cardImage" src={bald} />
                  <CardBody>
                    <CardTitle>Other</CardTitle>
                    <Button onClick={() => stylistType("California Waves")}>Select</Button>
                  </CardBody>
                </Card>

              </CardBody>
            </Card>
          </div>

        )

      case "Stylist":
        return (
          <div className="text-area">
            <Breadcrumb>
              <BreadcrumbItem>
                <a href="/">Home</a>
              </BreadcrumbItem>
              <BreadcrumbItem>Haircut</BreadcrumbItem>
              <BreadcrumbItem>Hair Style</BreadcrumbItem>
              <BreadcrumbItem active>Stylist</BreadcrumbItem>
            </Breadcrumb>
            <Card>
              <CardHeader>Book Appointment</CardHeader>
              <CardBody>
                <CardTitle>Choose Stylist</CardTitle>
                Below are stylists capable of doing your hair style.<br /><br />

                <Card className="cardSize">
                  <CardImg className="cardImage" src={shoulders} />
                  <CardBody>
                    <CardTitle>Tiffany</CardTitle>
                    <Button onClick={() => timeStylist("Tiffany")}>Select</Button>
                  </CardBody>
                </Card>

                <Card className="cardSize">
                  <CardImg className="cardImage" src={longtop} />
                  <CardBody>
                    <CardTitle>Nikki</CardTitle>
                    <Button onClick={() => timeStylist("Nikki")}>Select</Button>
                  </CardBody>
                </Card>

                <Card className="cardSize">
                  <CardImg className="cardImage" src={below} />
                  <CardBody>
                    <CardTitle>Danielle</CardTitle>
                    <Button onClick={() => timeStylist("Danielle")}>Select</Button>
                  </CardBody>
                </Card>

                <Card className="cardSize">
                  <CardImg className="cardImage" src={bald} />
                  <CardBody>
                    <CardTitle>Dave</CardTitle>
                    <Button onClick={() => timeStylist("Dave")}>Select</Button>
                  </CardBody>
                </Card>

              </CardBody>
            </Card>
          </div>
        )

      case "Time":
        return (

          <div className="text-area">
            <Breadcrumb>
              <BreadcrumbItem>
                <a href="/">Home</a>
              </BreadcrumbItem>
              <BreadcrumbItem>Haircut</BreadcrumbItem>
              <BreadcrumbItem>Hair Style</BreadcrumbItem>
              <BreadcrumbItem>Stylist</BreadcrumbItem>
              <BreadcrumbItem active>Time</BreadcrumbItem>
            </Breadcrumb>
            <Card>
              <CardHeader>Book Appointment</CardHeader>
              <CardBody>
                <CardTitle>Choose Stylist</CardTitle>

                <Card className="cardSize-Stylist">
                  <CardImg className="cardImage" src={shoulders} />
                  <CardBody>
                    <CardTitle>{stylist}</CardTitle>
                    Available:<br />
                    <strong>Mon-Fri</strong><br />
                    9am-5pm
                    <br />
                  </CardBody>
                </Card>

                <Card>
                  <CardHeader>Select A Time</CardHeader>
                  <CardBody>
                    <table className="timetable">
                      <tr>
                        <th>Time</th>
                        <th>Mon</th>
                        <th>Tue</th>
                        <th>Wed</th>
                        <th>Thu</th>
                        <th>Fri</th>
                        <th>Sat</th>
                        <th>Sun</th>
                      </tr>
                      <tr>
                        <td>9:00</td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Monday 9:00")}>Booked</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Tuesday 9:00")}>Book</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Wednesday 9:00")}>Book</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Thursday 9:00")}>Book</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Friday 9:00")}>Book</Button></td>
                        <td>-</td>
                        <td>-</td>
                      </tr>
                      <tr>
                        <td>10:00</td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Monday 10:00")}>Book</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Tuesday 10:00")}>Book</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Wednesday 10:00")}>Book</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Thursday 10:00")}>Book</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Friday 10:00")}>Book</Button></td>
                        <td>-</td>
                        <td>-</td>
                      </tr>
                      <tr>
                        <td>11:00</td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Monday 11:00")}>Booked</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Tuesday 11:00")}>Book</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Wednesday 11:00")}>Booked</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Thursday 11:00")}>Book</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Friday 11:00")}>Book</Button></td>
                        <td>-</td>
                        <td>-</td>
                      </tr>
                      <tr>
                        <td>12:00</td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Monday 12:00")}>Book</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Tuesday 12:00")}>Booked</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Wednesday 12:00")}>Book</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Thursday 12:00")}>Booked</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Friday 12:00")}>Book</Button></td>
                        <td>-</td>
                        <td>-</td>
                      </tr>
                      <tr>
                        <td>1:00</td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Monday 1:00")}>Booked</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Tuesday 1:00")}>Booked</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Wednesday 1:00")}>Booked</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Thursday 1:00")}>Book</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Friday 1:00")}>Book</Button></td>
                        <td>-</td>
                        <td>-</td>
                      </tr>
                      <tr>
                        <td>2:00</td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Monday 2:00")}>Booked</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Tuesday 2:00")}>Booked</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Wednesday 2:00")}>Booked</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Thursday 2:00")}>Booked</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Friday 2:00")}>Book</Button></td>
                        <td>-</td>
                        <td>-</td>
                      </tr>
                      <tr>
                        <td>3:00</td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Monday 3:00")}>Book</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Tuesday 3:00")}>Book</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Wednesday 3:00")}>Book</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Thursday 3:00")}>Booked</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Friday 3:00")}>Booked</Button></td>
                        <td>-</td>
                        <td>-</td>
                      </tr>
                      <tr>
                        <td>4:00</td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Monday 4:00")}>Booked</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Tuesday 4:00")}>Booked</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Wednesday 4:00")}>Booked</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Thursday 4:00")}>Booked</Button></td>
                        <td><Button theme="success" size="sm" onClick={() => confirm("Friday 4:00")}>Book</Button></td>
                        <td>-</td>
                        <td>-</td>
                      </tr>
                      <tr>
                        <td>5:00</td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Monday 5:00")}>Booked</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Tuesday 5:00")}>Booked</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Wednesday 5:00")}>Booked</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Thursday 5:00")}>Booked</Button></td>
                        <td><Button disabled theme="danger" size="sm" onClick={() => confirm("Friday 5:00")}>Booked</Button></td>
                        <td>-</td>
                        <td>-</td>
                      </tr>
                    </table>
                  </CardBody>
                </Card>

              </CardBody>
            </Card>
          </div>
        )


      case "Confirm":
        return (
          <div className="text-area">
            <Breadcrumb>
              <BreadcrumbItem>
                <a href="/">Home</a>
              </BreadcrumbItem>
              <BreadcrumbItem>Haircut</BreadcrumbItem>
              <BreadcrumbItem>Hair Style</BreadcrumbItem>
              <BreadcrumbItem>Stylist</BreadcrumbItem>
              <BreadcrumbItem>Time</BreadcrumbItem>
              <BreadcrumbItem active>Confirm</BreadcrumbItem>
            </Breadcrumb>
            <Card>
              <CardHeader>Book Appointment</CardHeader>
              <CardBody>
                <CardTitle>Confirm Your Appointment</CardTitle>
                Confirm the details below:<br /><br />

                <strong>Haircut:</strong> {haircut}<br/>
                <strong>Hair Style:</strong> {hairstyle}<br/>
                <strong>Stylist:</strong> {stylist}<br/>
                <strong>Time:</strong> {time}<br/><br/>
                <Button>Confirm</Button> &nbsp;
                <Button theme="danger">Cancel</Button>
              </CardBody>
            </Card>
          </div>
        )
      default:
        break;
    }
  }


  return (
    <div>
      <div className="welcome-area-profile">
        <Container>
          <Row>
            {showCards()}
          </Row>
        </Container>
      </div>
    </div>
  )
}