import React from "react"
import { Container, Row, Col } from "shards-react"
import "../App.css"

export default function footer() {
  return (
    <footer id="contact-us">
      <Container>
        <div className="footer-content">
          <Row>
            <Col>
              <div className="contact-form">
                <form id="contact" action="" method="post">
                  <Row>
                    <Col xs={6}>
                      <fieldset>
                        <input name="name" type="text" id="name" placeholder="Full Name" required="" />
                      </fieldset>
                    </Col>
                    <Col xs={6}>
                      <fieldset>
                        <input name="email" type="text" id="email" placeholder="E-Mail Address" />
                      </fieldset>
                    </Col>
                  </Row>
                  <Row>
                    <fieldset>
                      <textarea name="message" rows="6" id="message" placeholder="Your Message"
                        required=""></textarea>
                    </fieldset>
                  </Row>
                  <Row>
                    <fieldset>
                      <button type="submit" id="form-submit" className="main-button">Send Message</button>
                    </fieldset>
                  </Row>
                </form>
              </div>
            </Col>
            <Col className="right-content">
              <h2>More About <span>Uppercuts</span></h2>
              <p>Uppercuts is the premium place for haircuts and hair styles<br /><br />We always strive to deliver the best service we can. So if you have any suggestions or queries, pleaseuse the contact form to the left.</p>
            </Col>
          </Row>
        </div>
        <Row>
          <div className="col-lg-12">
            <div className="sub-footer">
              <p>Copyright &copy; 2021 Uppercut Hair Salon

                | Designed by Josh Thiele s3717991</p>
            </div>
          </div>
        </Row>
      </Container>
    </footer>
  )
}