import React, { useState } from "react"
import { Form, Modal, Button, ModalHeader, FormGroup, FormInput } from "shards-react"
import "../App.css"

export default function Login(props) {
  const [show, setShow] = useState(true)
  const [name, setName] = useState("")
  const [password, setPassword] = useState("")
  const [validated, setValidated] = useState(false)

  const handleClose = () => {
    setShow(false)
    props.handleClose()
  }

  async function handleSubmit(e) {
    //Checks form validation first.
    const form = e.currentTarget
    if (form.checkValidity() === false) {
      e.preventDefault()
      e.stopPropagation()
    } else {
      e.preventDefault()
      //Check to see if username is found. 
      try {
        const results = await fetch({
          method: "post",
          url: "http://localhost:8080/api/users/login",
          data: {
            name: name
          },
          withCredentials: true,
        })
        console.log(results.data)
        //If data is null, username or password is wrong. Only name is send if correct for security
        if (results.data === null) {
          //TODO: Create Alert here about incorrect username or password
        } else {
          console.log()
          if (true) {
            window.location.href = "/profile"
            handleClose()
          }
        }


        //Log simple data to localstorage so database isn't contacted as much. NOTE: Data should not be sensitive. 
      } catch (error) {
        console.log(error)
      }
    }

    //const results = await validateForm()
    //console.log(results)

    setValidated(true)
  }

  return (
    <Modal>
      <ModalHeader>
        Login
      </ModalHeader>
        <div className="mini-font">
          <p>
            New to VibeCheck? <button className="register" onClick={() => { props.handleSwitch() }}>Register Here</button></p>
        </div>
        <div className="contact-form-login">
          <Form noValidate validated={validated} onSubmit={handleSubmit}>
            <FormGroup size="lg" controlId="email">
                <FormInput autoFocus required type="text" value={name} onChange={(e) => setName(e.target.value)} placeholder="username" />
            </FormGroup>
            <FormGroup size="lg" controlId="password">
                <FormInput
                  type="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  placeholder="password"
                  required
                />
            </FormGroup>
            <Button block size="lg" type="submit">
              Login
            </Button>
          </Form>
        </div>
    </Modal>
  )
}
